    .wp-block-my-block-secondblock.has-shadow.shadow-opacity-20 {
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.2); }
    .wp-block-my-block-secondblock.has-shadow.shadow-opacity-30 {
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.3); }
